import React, { useState, useEffect } from 'react';
import './App.css';
import LoginPage from './components/LoginPage';
import DataEntryPage from './components/DataEntryPage';
import StudentDashboard from './components/StudentDashboard';
import AnalyticsDashboard from './components/AnalyticsDashboard';
import { supabase } from './supabase'; //  supabase 

function App() {
  const [auth, setAuth] = useState({
    isLoggedIn: false,
    role: null,
    studentId: null
  });
  const [view, setView] = useState('dashboard'); // 'dashboard', 'dataEntry', 'analytics'

  //    
  useEffect(() => {
    const checkSession = async () => {
      //    Supabase
      const { data: { session } } = await supabase.auth.getSession();
      
      if (session) {
        //   
        const { data: profileData, error: profileError } = await supabase
          .from('profiles')
          .select('*')
          .eq('id', session.user.id)
          .single();
          
        if (profileData && !profileError) {
          //   
          const newAuth = {
            isLoggedIn: true,
            role: profileData.role,
            studentId: profileData.student_id
          };
          
          setAuth(newAuth);
          
          //       
          if (profileData.role === 'analytics') {
            setView('analytics');
          } else if (profileData.role === 'teacher') {
            setView('dataEntry');
          } else {
            setView('dashboard');
          }
        }
      }
    };
    
    checkSession();
  }, []);

  //   
  const handleLogin = async (userData) => {
    try {
      //   Supabase
      const { data, error } = await supabase.auth.signInWithPassword({
        email: userData.email,
        password: userData.password
      });
      
      if (error) throw error;
      
      //   
      const { data: profileData, error: profileError } = await supabase
        .from('profiles')
        .select('*')
        .eq('id', data.user.id)
        .single();
        
      if (profileError) throw profileError;
      
      //   
      const newAuth = {
        isLoggedIn: true,
        role: profileData.role,
        studentId: profileData.student_id
      };
      
      setAuth(newAuth);
      
      //       
      if (profileData.role === 'analytics') {
        setView('analytics');
      } else if (profileData.role === 'teacher') {
        setView('dataEntry');
      } else {
        setView('dashboard');
      }
      
    } catch (error) {
      console.error('Error logging in:', error.message);
      alert(' : ' + error.message);
    }
  };

  //   
  const handleLogout = async () => {
    //   Supabase
    await supabase.auth.signOut();
    
    //  
    setAuth({
      isLoggedIn: false,
      role: null,
      studentId: null
    });
    setView('dashboard');
  };

  // ,   
  const renderContent = () => {
    if (!auth.isLoggedIn) {
      return <LoginPage onLogin={handleLogin} />;
    }

    //   - ,      
    if (auth.role === 'student') {
      return (
        <div className="dashboard-wrapper">
          <div className="dashboard-navbar">
            <span className="user-role"></span>
            <button className="logout-btn" onClick={handleLogout}>
              
            </button>
          </div>
          <StudentDashboard studentId={auth.studentId} userRole={auth.role} />
        </div>
      );
    }

    //   - ,   
    if (auth.role === 'analytics') {
      return (
        <div className="dashboard-wrapper">
          <div className="dashboard-navbar">
            <span className="user-role"></span>
            <button 
              className={view === 'analytics' ? 'active' : ''}
              onClick={() => setView('analytics')}
            >
              
            </button>
            <button className="logout-btn" onClick={handleLogout}>
              
            </button>
          </div>
          <AnalyticsDashboard userRole={auth.role} />
        </div>
      );
    }

    //   - ,    
    if (auth.role === 'teacher') {
      return (
        <div className="dashboard-wrapper">
          <div className="dashboard-navbar">
            <span className="user-role"></span>
            <button 
              className={view === 'dataEntry' ? 'active' : ''}
              onClick={() => setView('dataEntry')}
            >
               
            </button>
            <button className="logout-btn" onClick={handleLogout}>
              
            </button>
          </div>
          <DataEntryPage onLogout={handleLogout} userRole={auth.role} />
        </div>
      );
    }

    //    
    if (auth.role === 'admin') {
      if (view === 'dataEntry') {
        return (
          <div className="dashboard-wrapper">
            <div className="dashboard-navbar">
              <span className="user-role"></span>
              <button 
                className={view === 'dashboard' ? 'active' : ''}
                onClick={() => setView('dashboard')}
              >
                
              </button>
              <button 
                className={view === 'analytics' ? 'active' : ''}
                onClick={() => setView('analytics')}
              >
                
              </button>
              <button 
                className={view === 'dataEntry' ? 'active' : ''}
                onClick={() => setView('dataEntry')}
              >
                 
              </button>
              <button className="logout-btn" onClick={handleLogout}>
                
              </button>
            </div>
            <DataEntryPage onLogout={handleLogout} userRole={auth.role} />
          </div>
        );
      } else if (view === 'analytics') {
        return (
          <div className="dashboard-wrapper">
            <div className="dashboard-navbar">
              <span className="user-role"></span>
              <button 
                className={view === 'dashboard' ? 'active' : ''}
                onClick={() => setView('dashboard')}
              >
                
              </button>
              <button 
                className={view === 'analytics' ? 'active' : ''}
                onClick={() => setView('analytics')}
              >
                
              </button>
              <button 
                className={view === 'dataEntry' ? 'active' : ''}
                onClick={() => setView('dataEntry')}
              >
                 
              </button>
              <button className="logout-btn" onClick={handleLogout}>
                
              </button>
            </div>
            <AnalyticsDashboard userRole={auth.role} />
          </div>
        );
      } else {
        return (
          <div className="dashboard-wrapper">
            <div className="dashboard-navbar">
              <span className="user-role"></span>
              <button 
                className={view === 'dashboard' ? 'active' : ''}
                onClick={() => setView('dashboard')}
              >
                
              </button>
              <button 
                className={view === 'analytics' ? 'active' : ''}
                onClick={() => setView('analytics')}
              >
                
              </button>
              <button 
                className={view === 'dataEntry' ? 'active' : ''}
                onClick={() => setView('dataEntry')}
              >
                 
              </button>
              <button className="logout-btn" onClick={handleLogout}>
                
              </button>
            </div>
            <StudentDashboard userRole={auth.role} />
          </div>
        );
      }
    }
  };

  return (
    <div className="app">
      {renderContent()}
    </div>
  );
}

export default App;